.ConatctContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.IntroTextContainer {
    text-align: center;
    max-width: 80%;
}
.IntroTextContainer >h1{
    text-transform: uppercase;
}

.emailSender {
    background-color: #073648;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    gap: 10px;
    width: 600px;
    height: 600px;
    border-radius: 5px;
    margin-bottom: 50px;
}
.emailSender>label{
    color: #fff;
    text-transform: uppercase;
    font-weight: bolder;
}
.emailSender>input {
    width: 500px;
    border-radius: 5px;
}

.emailSender>textarea {
    border-radius: 5px;
    resize: none;
    width: 500px;
    height: 300px;
}
.emailSender textarea::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}


.emailSender textarea::-webkit-scrollbar-track {
    border-radius: 3px;
    background-color: rgba(179, 179, 179, 0.434);
}


.emailSender textarea::-webkit-scrollbar-thumb {
    background: #073648;

    border-radius: 3px;
    border-color: rgba(179, 179, 179, 0.434);
}

.emailSender textarea::-webkit-scrollbar-thumb:hover {
    background: #09435a;
}
.WarningTextContainer{
    margin-left: auto;
    margin-right: auto;
    max-width: 75%;
    display: flex;
    justify-content: center;
    background-color: crimson;
    align-items: center;
    border-radius: 5px;
    padding: 15px;
}
.WarningTextContainer >p {
    color: #fff;
    text-transform: uppercase;
    font-weight: bolder;
}